@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Encode+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@700&display=swap);
/* .App {
  font-family: sans-serif;
  text-align: center;
} */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  /* font-family: "Encode Sans", sans-serif; */
  /* font-family: "Ubuntu", sans-serif; */
  /* font-family: "Poppins", sans-serif; */
  font-family: "JetBrains Mono", monospace;
}

.header h1 {
  font-family: "Ubuntu", sans-serif;
}
.taskText,
.taskTime {
  font-family: "JetBrains Mono", monospace;
  font-size: 15px;
}
.taskTime {
  font-size: 13px;
}

.container {
  max-width: 350px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 3px solid #0c3e75;
  padding: 30px;
  border-radius: 5px;
  /* ***************** */
  background-image: url("https://marburg.com/wp-content/uploads/2020/01/31602-800x1000.jpg");
  background-size: cover;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 12px;
  /* font-family: inherit; */
  font-family: "JetBrains Mono", monospace;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  border: 2px solid lightgoldenrodyellow;
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  /* ################# */
  border-radius: 3px;
  background-image: linear-gradient(to right, blanchedalmond, cornsilk, linen);
  /* background-size: cover; */
}

.task.reminder {
  border-left: 5px solid sienna;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
  padding: 10px;
  padding-right: 20px;

  border: 2px solid rgb(207, 162, 141);
  border-radius: 3px;
  background-image: linear-gradient(
    to right,
    blanchedalmond,
    cornsilk,
    blanchedalmond
  );
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
  font-family: "Ubuntu", sans-serif;
  color: darkslategray;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
  border-radius: 5px;
  background-color: rgb(255, 247, 233);
  font-family: "JetBrains Mono", monospace;
  font-size: 15px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1 1;
}

.form-control-check input {
  flex: 2 1;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
  color: darkslategray;
  font-size: 14px;
  font-style: italic;
}
footer a {
  color: navy;
  font-style: normal;
}

